@font-face {
  font-weight: normal;
  font-family: "PT Mono";
  font-style: normal;
  src: url("../fonts/PT_Mono/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-weight: medium;
  font-family: "Roboto";
  font-style: normal;
  src: url("../fonts/Roboto/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Medium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-weight: normal;
  font-family: "Roboto Condensed";
  font-style: normal;
  src: url("../fonts/Roboto_Condensed/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/Roboto_Condensed/RobotoCondensed-Regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-weight: bold;
  font-family: "Roboto Condensed";
  font-style: normal;
  src: url("../fonts/Roboto_Condensed/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/Roboto_Condensed/RobotoCondensed-Bold.woff") format("woff");
  font-display: swap; }

body {
  font-family: "PT Mono", "Courier", monospace;
  margin: 0 auto;
  padding: 0; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.container {
  width: 74%;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      width: 94%; } }
  @media (max-width: 767px) {
    .container {
      width: 91%; } }

.content-wrapper {
  width: 60%;
  margin: 0 auto; }

.btn {
  border-radius: 5px;
  box-sizing: border-box;
  font-family: "Roboto Condensed";
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.05em; }

.page-header {
  padding-top: 33px;
  padding-bottom: 263px;
  color: #ffffff;
  background-image: url("../img/bg-header-desktop@1x.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #1b2931; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-header-desktop@2x.jpg"); } }
  .page-header__wrapper {
    display: flex;
    justify-content: space-between; }
  .page-header a {
    color: #ffffff;
    text-decoration: none; }
  .page-header__contacts {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 660px; }
  .page-header__email-wrapper {
    position: relative;
    padding-left: 40px;
    box-sizing: border-box; }
  .page-header__icon-email {
    position: absolute;
    top: 3px;
    left: 0; }
  .page-header__email {
    display: block;
    font-size: 16px; }
  .page-header__phone-wrapper {
    position: relative;
    padding-left: 35px;
    box-sizing: border-box; }
  .page-header__icon-phone {
    position: absolute;
    top: 0;
    left: 0; }
  .page-header__phone {
    display: block;
    font-size: 16px; }
  .page-header__callback {
    width: 180px;
    padding: 13px 0 12px;
    font-size: 14px;
    border: 1px solid #ffffff; }
  .page-header__scroll {
    display: block;
    width: 93px;
    font-family: "PT Mono";
    font-size: 14px;
    font-weight: normal;
    opacity: 0.5;
    transform: translate(-35px, 130px) rotate(-90deg); }
  @media (max-width: 1023px) {
    .page-header {
      padding-top: 20px;
      padding-bottom: 163px;
      background-image: url("../img/bg-header-tablet@1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-header-tablet@2x.jpg"); } }
  @media (max-width: 1023px) {
      .page-header__contacts {
        width: 83px; }
      .page-header__contacts-text {
        display: none; }
      .page-header__callback {
        display: none; }
      .page-header__email-wrapper {
        padding-left: 0; }
      .page-header__phone-wrapper {
        padding-left: 0; }
      .page-header__email {
        width: 25px;
        height: 19px; }
      .page-header__icon-email {
        top: -8px;
        width: 25px;
        height: 19px; }
      .page-header__phone {
        width: 24px;
        height: 24px; }
      .page-header__icon-phone {
        top: -8px;
        width: 24px;
        height: 24px; }
      .page-header__scroll {
        display: none; } }
  @media (max-width: 767px) {
    .page-header {
      padding-top: 17px;
      padding-bottom: 70px;
      background-image: url("../img/bg-header-mobile@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-header-mobile@2x.jpg"); } }
  @media (max-width: 767px) {
      .page-header__contacts {
        width: 73px; }
      .page-header__icon-email {
        top: 0; }
      .page-header__icon-phone {
        top: 0; } }

.logo {
  display: flex;
  width: 201px;
  margin-right: 50px; }
  .logo img {
    width: 52px;
    height: 52px;
    margin-right: 16px; }
  .logo__wrapper h1 {
    padding: 0;
    margin: 0;
    font-family: "Roboto Condensed";
    font-size: 25px;
    font-weight: bold;
    white-space: nowrap; }
  .logo__wrapper h2 {
    padding: 0;
    margin: 0;
    font-family: "Roboto";
    font-size: 12px;
    font-weight: medium;
    color: #788891; }
  @media (max-width: 767px) {
    .logo {
      width: 38px; }
      .logo img {
        width: 38px;
        height: 38px; }
      .logo__wrapper h1 {
        display: none; }
      .logo__wrapper h2 {
        display: none; } }

.header-content {
  width: 718px;
  padding-top: 231px;
  box-sizing: border-box; }
  .header-content h2 {
    padding: 0;
    margin: 0;
    margin-bottom: 31px;
    font-family: "Roboto Condensed";
    font-size: 80px;
    font-weight: bold;
    letter-spacing: 0.07em;
    text-transform: uppercase; }
  .header-content__wrapper {
    position: relative;
    padding-left: 95px; }
    .header-content__wrapper::before {
      content: "";
      position: absolute;
      top: 17px;
      left: 5px;
      width: 66px;
      height: 2px;
      background-color: #ffffff; }
  .header-content p {
    padding: 0;
    margin: 0;
    margin-bottom: 37px;
    font-family: "PT Mono";
    font-size: 22px;
    font-weight: normal;
    line-height: 156.8%; }
  .header-content a {
    display: block;
    width: 407px;
    padding: 19px 0 18px;
    font-size: 16px;
    background-color: #00b2ff; }
  .header-content__mobile-btn-text {
    display: none; }
  @media (max-width: 1023px) {
    .header-content {
      width: 470px;
      padding-top: 116px;
      padding-left: 66px; }
      .header-content h2 {
        font-size: 60px;
        margin-bottom: 42px; }
      .header-content__wrapper {
        padding-left: 50px; }
        .header-content__wrapper::before {
          top: 16px;
          width: 33px; }
      .header-content p {
        font-size: 18px;
        margin-bottom: 44px; }
      .header-content a {
        width: 379px; } }
  @media (max-width: 767px) {
    .header-content {
      width: 277px;
      padding-top: 231px;
      padding-left: 0; }
      .header-content h2 {
        font-size: 40px;
        margin-bottom: 30px; }
      .header-content__wrapper {
        padding-left: 0; }
      .header-content p {
        font-size: 16px;
        padding-left: 51px;
        margin-bottom: 42px; }
      .header-content a {
        width: 287px; }
      .header-content__btn-text {
        display: none; }
      .header-content__mobile-btn-text {
        display: inline; } }
